import React, { useState } from 'react';
import { marked } from 'marked';
import DOMPurify from 'dompurify';

const ClaudeAnalysis = ({ analysis, loading }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  if (loading) {
    return (
      <div className="claude-analysis">
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Anlisis Avanzado con Claude</h3>
          </div>
          <div className="analysis-loading">
            <div className="loading-spinner"></div>
            <div className="loading-text">Generando anlisis detallado...</div>
          </div>
        </div>
      </div>
    );
  }

  if (!analysis || analysis.trim() === '') {
    return (
      <div className="claude-analysis">
        <div className="card">
          <div className="card-header">
            <h3 className="card-title">Anlisis Avanzado con Claude</h3>
          </div>
          <div className="no-analysis">
            <p>No hay anlisis disponible en este momento.</p>
          </div>
        </div>
      </div>
    );
  }

  const parseAnalysis = (text) => {
    try {
      // Configure marked options
      marked.setOptions({
        breaks: true,
        gfm: true,
        headerIds: false,
        mangle: false
      });

      // Convert markdown to HTML
      const html = marked(text);
      
      // Sanitize HTML
      const cleanHtml = DOMPurify.sanitize(html, {
        ALLOWED_TAGS: ['p', 'br', 'strong', 'em', 'ul', 'ol', 'li', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'blockquote', 'code', 'pre'],
        ALLOWED_ATTR: []
      });

      return cleanHtml;
    } catch (error) {
      console.error('Error parsing analysis:', error);
      return `<p>${text}</p>`;
    }
  };

  const analysisHtml = parseAnalysis(analysis);
  const analysisPreview = analysis.substring(0, 300) + (analysis.length > 300 ? '...' : '');

  return (
    <div className="claude-analysis">
      <div className="card">
        <div className="card-header">
          <h3 className="card-title">
            <span className="claude-icon"></span>
            Anlisis Avanzado con Claude
          </h3>
          <p className="analysis-description">
            Anlisis detallado de tu portafolio basado en inteligencia artificial
          </p>
        </div>
        
        <div className="analysis-content">
          {!isExpanded ? (
            <div className="analysis-preview">
              <div className="preview-text">
                {analysisPreview}
              </div>
              <button 
                className="btn btn-secondary expand-btn"
                onClick={() => setIsExpanded(true)}
              >
                Ver Anlisis Completo
              </button>
            </div>
          ) : (
            <div className="analysis-full">
              <div 
                className="analysis-text"
                dangerouslySetInnerHTML={{ __html: analysisHtml }}
              />
              <button 
                className="btn btn-secondary collapse-btn"
                onClick={() => setIsExpanded(false)}
              >
                Ocultar Anlisis
              </button>
            </div>
          )}
        </div>

        <div className="analysis-footer">
          <div className="analysis-disclaimer">
            <p>
              <strong>Importante:</strong> Este anlisis es generado por inteligencia artificial y debe ser considerado como informacin complementaria. 
              Siempre consulta con un asesor financiero profesional antes de tomar decisiones de inversin.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ClaudeAnalysis; 